@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600&family=Poppins:wght@500;600;700;800&display=swap');


/* Declaration des couleurs , fonts, sizes and weights */
:root {
    /* colors */
    /* hsl(Hue(teinte : couleur base (rouge,bleu ou vert) , Saturation : l'intensité du couleur , Ligntness : luminosité de la couleur )  */
    --first-color: #5BAD7E;
    --title-color: #63635F;
    --text-color: #7c7c7c;
    --body-color: hsl(0,0%,100%);
    --container-color: hsl(0,0%,93%);
    --dark-color: hsl(0,0%,93%);
    --border-color: hsl(0,0%,87%);
    --text-white: #fff;


    /* Fonts */
    --body-font : 'Open Sans', sans-serif;
    --second-font: 'Poppins', sans-serif;

    --big-font-size: 56px;
    --h1-font-size: 50px;
    --h2-font-size: 32px;
    --h3-font-size: 24px;
    --large-font-size: 18px;
    --normal-font-size: 16px;
    --small-font-size: 15px;
    --smaller-font-size: 14px;
    --tiny-font-size: 12px;

    /* Fonts weight*/
    --weight-400 :400;
    --weight-500 :500;
    --weight-600 :600;
    --weight-700 :700;
    --weight-800 :800;

}

.dark-theme{
    --title-color: hsl(0 , 0% , 100%);
    --text-color:  hsl(0 , 0%,80%);
    --body-color: hsl(0,0%,7%);
    --container-color: hsl(0,1%,17%);
    --dark-color: hsl(0,0%,93%);
    --border-color: hsl(0,0%,15%);
}

/* Base */

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, input, textarea {
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    font-weight: var(--weight-500);
}


body{
    background-color: var(--body-color);
    color: var(--text-color);
}

h1,h2,h3,h4{
    color: var(--title-color);
    font-family: var(--second-font);
    font-weight: var(--weight-700);
}


ul {
    list-style: none;
}

a{
    text-decoration: none;
}

button, input, textarea{
    background-color: transparent;
    border: none;
    outline: none;
}

img {
    max-width: 100%;
}

.container{
    max-width: 1140px;
    margin-inline: auto;
    padding-inline: 15px;
}

.grid {
    display: grid;
    gap: 30px;
}

.section{
    padding-block: 70px;
}

.section__title{
    font-size: var(--big-font-size);
    font-weight: var(--weight-800);
    text-align: center;
    margin-bottom: 70px;
}

.section__title span{
    color: var(--first-color);
}

.button {
    display: inline-block;
    color: var(--title-color);
    border: 1px solid var(--first-color);
    padding: 15px 70px 15px 35px;
    border-radius: 35px;
    font-family: var(--second-font);
    font-size: var(--small-font-size);
    font-weight: var(--weight-500);
    position: relative;
    overflow: hidden;
    transition: all 0.25s ease-in-out;
}

.button:hover {
    color: var(--text-white);
}

.button::before {
    content: '';
    position: absolute;
    inset: 0;
    background-color: var(--first-color);
    transform: translateX(100%);
    z-index: -1;
    transition: all 0.3s ease-out ;
}

.button:hover::before{
    transform: translateX(0);
}

.button__icon {
    background-color: var(--first-color);
    position: absolute;
    inset-block: -2px;
    right: 0;
    width: 55px;
    border-radius: 50%;
    color: var(--text-white);
    font-size: var(--large-font-size);
    text-align: center;
    line-height: 60px;
}
